<template>
  <div>
    <!-- :index-list自定义索引列 -->
    <van-index-bar :index-list="indexList" @change="handleChange">
      <div v-for="itemx in cityList" :key="itemx.type">
        <van-index-anchor :index="itemx.type" />
        <van-cell :title="itemy.name" v-for="itemy in itemx.list" :key="itemy.cityId" @click="handeChangeCity(itemy.cityId, itemy.name)" />
      </div>
    </van-index-bar>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import { Toast } from 'vant'
export default {
  name: 'City',
  data() {
    return {
      cityList: [],
    }
  },
  mounted() {
    this.getCity()
  },
  methods: {
    ...mapMutations(['changeCity']),
    async getCity() {
      const { data: res } = await this.$api.GetCityList()
      // console.log(res.cities)
      this.handleCity(res.cities)
    },
    handeChangeCity(id, name) {
      this.changeCity({ id, name })
      this.$router.back()
    },
    handleCity(citys) {
      // 生成26个大写字母
      let zimuList = []
      for (let i = 65; i < 91; i++) {
        zimuList.push(String.fromCharCode(i))
      }
      // console.log(zimuList);
      zimuList.forEach((zimu) => {
        const newList = citys.filter((item) => item.pinyin.substring(0, 1).toUpperCase() === zimu)
        if (newList.length > 0) {
          this.cityList.push({
            type: zimu,
            list: newList,
          })
        }
      })
    },
    handleChange(index) {
      // console.log(index);
      Toast({
        message: index,
        duration: 500,
      })
    },
  },
  computed: {
    indexList() {
      return this.cityList.map((item) => item.type)
    },
  },
}
</script>

<style lang="less" scoped></style>
